* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: Arial, sans-serif;
}

.custom-container {
    width: 100%;
    padding: 0 15px;
    margin: 0 auto;
}

.custom-row {
    display: flex;
    flex-wrap: wrap;
    margin: -15px; /* Adds gutter between columns */
}

[class*="custom-col-"] {
    padding: 15px; /* Adds gutter inside columns */
}

.custom-col-12 {
    flex: 0 0 100%; /* Full width on small screens */
    max-width: 100%;
}

.custom-col-md-6 {
    flex: 0 0 50%; /* 50% width on medium screens */
    max-width: 50%;
}

.custom-col-lg-4 {
    flex: 0 0 33.33%; /* 33.33% width on large screens */
    max-width: 33.33%;
}

/* Responsive breakpoints */
@media (min-width: 576px) {
    .custom-col-sm-12 {
        flex: 0 0 100%;
        max-width: 100%;
    }
}

@media (min-width: 768px) {
    .custom-col-md-6 {
        flex: 0 0 50%;
        max-width: 50%;
    }
}

@media (min-width: 992px) {
    .custom-col-lg-4 {
        flex: 0 0 33.33%;
        max-width: 33.33%;
    }
}
